// ------------------------------------
// Modals

.modal-backdrop {
  background-color: @brand-primary;

  &.in {
    opacity: 0.9;
  }
}
.modal-alert {
  text-align: center;
}
.modal-body {
  /*background-color: @fl-body-secondary-color;
  padding: 25px;*/

  & .form-control {
    background-color: #fff;
  }
}
.modal-footer {
  border: 0;
  padding: 20px;
  text-align: center;
  color: @brand-primary-light-text;
}
.modal-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  pointer-events: none;
  border-radius: @border-radius-base;
  .transition(opacity 0.2s);

  &.active {
    opacity: 1;
    pointer-events: auto;
  }
}
.form-centered {
  text-align: center;

  & .form-control, & .btn {
    width: 250px;
    margin: 0 auto;
    text-align: center;
  }
}

.modal-confirm {
  .modal-header {
    padding-bottom: 10px;
  }
  .modal-body {
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .modal-footer {
    padding-top: 10px;
  }
}

@media @phone {
  .modal.fade {
    opacity: 1;
  }
  .modal-backdrop.in {
    opacity: 0;
  }
  .modal-dialog {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
  }
  .modal-dialog {
    margin: 0;

    .modal.fade & {
      .transition(top 0.3s);
      top: 100%;
      .translate(0, 0);
    }
    .modal.in & {
      top: 0;
    }

    &:before {
      content: " ";
      /*.toolbar();*/
    }
  }
  .modal-content {
    border-radius: 0;
    border: 0;
    min-height: 100vh;
    /*padding-top: @mobile-header-height;*/
    .box-shadow(none);
  }
  .modal-header {
    padding: 0;
    border: 0;
    min-height: 0;

    & h3 {
      line-height: 36px;
    }
  }
}

@media @tablet, @desktop, @desktop-hd {
  .modal-dialog {
    margin: 120px auto;

    & .close {
      position: absolute;
      right: 5px;
      top: 5px;
      z-index: 1;
    }
  }
  .modal-content {
    border: 0;
    border-radius: @border-radius-base;
    .box-shadow(0 7px 15px @gray);
  }
  .modal-sm {
    width: 375px;
  }
  .modal-header {
    text-align: center;
    border: 0;
    padding: 25px;

    & h3 {
      font-size: 22px;
      margin: 0;
    }
  }
}
