#wrapper.toggled #page-content-wrapper {
  position: absolute;
  margin-right: -250px;
}
#sidebar-wrapper.dark {
  background: #3d5d8a;
}
/* Sidebar Styles */
.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.sidebar-nav li {
  text-indent: 20px;
  line-height: 40px;
}
.sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: #434547;
}
.sidebar-nav li a .category-icon {
  margin-right: 10px;
}
.sidebar-nav li a:hover,
.sidebar-nav li.active a {
  text-decoration: none;
  color: #386db8;
  background: #E5E6EC;
}
.sidebar-nav li.active a {
  font-weight: bold;
}
.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}
.sidebar-nav > .sidebar-title {
  height: 65px;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 60px;
  color: #333;
}
#sidebar-wrapper.dark .sidebar-nav li a {
  color: #a7b5c8;
}
#sidebar-wrapper.dark .sidebar-nav li a:hover,
#sidebar-wrapper.dark .sidebar-nav li.active a {
  color: #fff;
  background: rgba(255, 255, 255, 0.2);
}
#sidebar-wrapper.dark .sidebar-nav > .sidebar-title {
  color: #fff;
}
.yt-video {
  height: 300px;
}
.new_chat_alert,
.spam_alert {
  position: absolute;
  bottom: 5px;
  background: rgba(255, 255, 255, 0.9);
  margin-left: 40px;
  border-radius: 4px;
  border: 2px solid #386db8;
}
.new_chat_alert a,
.spam_alert a {
  display: inline-block;
  padding: 5px 20px;
}
.spam_alert {
  padding: 5px 20px;
  right: 10px;
  border-color: #f29f97;
  color: #E74C3C;
}
@media (min-width: 768px) {
  #wrapper {
    padding-left: 250px;
    padding-right: 250px;
  }
  #wrapper.toggled {
    padding-left: 0;
    padding-right: 0;
  }
  #sidebar-wrapper,
  #sidebar-wrapper-right {
    width: 250px;
  }
  #wrapper.toggled #sidebar-wrapper,
  #wrapper.toggled #sidebar-wrapper-right {
    width: 0;
  }
  #page-content-wrapper {
    padding: 20px;
    position: relative;
    margin-top: 52px;
  }
  #wrapper.toggled #page-content-wrapper {
    position: relative;
    margin-right: 0;
  }
  .stream#wrapper {
    padding-right: 600px;
  }
  .stream #sidebar-wrapper-right {
    width: 600px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .stream#wrapper {
    padding-right: 450px;
  }
  .stream #sidebar-wrapper-right {
    width: 450px;
  }
}
@media (min-width: 1025px) {
  .yt-video {
    height: 360px;
  }
}
.sa-wrapper{
    width:100%;
    position:relative;
}
.sa-realArea, .sa-fakeArea{
    color: inherit;
    resize: none;
    overflow: hidden;
    z-index:0;
    width:100%;
}

textarea.sa-realArea{
    -webkit-text-fill-color: transparent;
    position:absolute;
    top:0;
    left:0;
    background: transparent;
    border-color: transparent;
}
textarea.sa-realArea:hover, textarea.sa-realArea:focus{
    border-color: transparent;
}

.sa-fakeArea{
    color:#000;
    height:100%;
    width:100% !important;
}

.sa-dropdown{
    position: absolute;
    min-width: 100px;
    text-align: left;
    z-index:1;
}
.sa-dropdown .dropdown-menu{
    display:block;
}

.sa-dropdown a{
    padding: 2px 6px;
    font-size: 0.9em;
}

.sa-dropdown a.active{
    background-color:#4497de;
    color:#fff;
}

