#owning-buttons {
  text-align: center;
}
#owning-buttons .btn.active {
  box-shadow: none;
}
#owning-buttons .btn-success.active {
  background-color: #21b384;
}
#owning-buttons .btn-primary.active {
  background-color: #386db8;
}
#owning-buttons .btn-warning.active {
  background-color: #f8c36e;
  color: #976008;
}
#owning-buttons .btn-group {
  margin: 0 auto;
  text-align: center;
  width: inherit;
  display: inline-block;
}
#owning-buttons .btn-group label.btn {
  padding-left: 2em;
  padding-right: 2em;
}
#owning-buttons.no-bg .btn-default {
  border-color: #3f597e;
}
#buy-buttons {
  margin-bottom: 15px;
  text-align: center;
}
#buy-buttons a {
  width: 380px;
}
#buy-buttons a .fa-shopping-cart {
  margin-right: 10px;
}
.info-bar {
  color: #5A5A5A;
  padding: 0 0 20px;
}
.info-bar .popularity {
  font-size: 18px;
  color: #386db8;
}
.info-bar .face {
  display: inline-block;
  background-image: url('/images/faces.png');
  height: 24px;
  width: 24px;
  background-size: auto 24px;
  opacity: 0.5;
}
.info-bar .face:hover {
  opacity: 1;
}
.info-bar .face.face-wow {
  background-position: 1px 0;
}
.info-bar .face.face-yes {
  background-position: -32px 0;
}
.info-bar .face.face-maybe {
  background-position: -65px 0;
}
.info-bar .face.face-no {
  background-position: -97px 0;
}
.info-bar .face-square {
  cursor: pointer;
  padding: 5px;
  border-radius: 2px;
}
.info-bar .face-square:hover .face {
  opacity: 1;
}
.info-bar .face-square.active {
  color: #386db8;
  font-weight: bold;
  cursor: default;
}
.info-bar .face-square.active .face {
  background-image: url('/images/faces_blue.png');
  opacity: 1;
}
.info-bar .face-square .dialog {
  position: absolute;
  left: 48px;
  top: 0;
  font-size: 10px;
  text-transform: uppercase;
  text-align: center;
  padding: 4px 5px;
  margin-left: 10px;
  background: #E5E7EC;
  border-radius: 4px;
  min-width: 35px;
  color: #888;
}
.info-bar .face-square:hover .dialog {
  color: #333;
}
.info-bar .face-square .dialog:before {
  content: "";
  position: absolute;
  bottom: -20px;
  left: 50px;
  border-style: solid;
  display: block;
  width: 0;
  top: 14px;
  left: -10px;
  bottom: auto;
  border-width: 0px 0px 8px 15px;
  border-color: #e5e7ec transparent;
}
.info-bar .face-square.active .dialog:before {
  border-color: #386db8 transparent;
}
.info-bar .face-square.active .dialog {
  background: #386db8;
  color: #fff;
}
.info-bar .face-square .percentage {
  font-size: 13px;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
}
#comparametrics h3 {
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  border-bottom: 1px solid #CCC;
}
#comparametrics h3 .product-name {
  color: #386db8;
}
#nps-question {
  margin-top: 15px;
  border-top: 1px solid #f1f1f1;
  padding-top: 15px;
}
#nps-question #question {
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
}
#nps-question table {
  width: 100%;
}
#nps-question table td {
  text-align: center;
}
#nps-question .voting-circle {
  display: inline-block;
  border: 3px solid #dadada;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 36px;
  font-weight: 600;
  color: #888;
}
#nps-question .voting-circle:hover {
  cursor: pointer;
}
#nps-question .voting-circle.selected,
#nps-question .voting-circle .voting-circle:hover {
  cursor: pointer;
  border-color: #444;
  color: #444;
}
#nps-question .voting-circle.good.selected,
#nps-question .voting-circle.good:hover {
  border-color: #76D786;
}
#nps-question .voting-circle.bad.selected,
#nps-question .voting-circle.bad:hover {
  border-color: #FFA18B;
}
#nps-question .voting-circle.medium.selected,
#nps-question .voting-circle.medium:hover {
  border-color: #EFF0A6;
}
@media (max-width: 767px) {
  #nps-question .voting-circle {
    border-width: 2px;
    width: 36px;
    height: 36px;
    line-height: 32px;
  }
}
hr.star-light,
hr.star-primary {
  margin: 25px auto 30px;
  padding: 0;
  max-width: 250px;
  border: 0;
  border-top: solid 5px;
  text-align: center;
}
hr.star-light:after,
hr.star-primary:after {
  content: "\f005";
  display: inline-block;
  position: relative;
  top: -0.8em;
  padding: 0 .25em;
  font-family: FontAwesome;
  font-size: 2em;
}
hr.star-light {
  border-color: #fff;
}
hr.star-light:after {
  color: #fff;
  background-color: #386db8;
}
hr.star-primary {
  border-color: #2c3e50;
}
hr.star-primary:after {
  color: #2c3e50;
  background-color: #fff;
}
header {
  text-align: center;
  color: #fff;
  background: #386db8;
  box-shadow: inset 0 -10px 10px -10px rgba(0, 0, 0, 0.25);
  margin-top: 58px;
  overflow: hidden;
  position: relative;
}
.fullscreen-bg__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
@media (min-aspect-ratio: 16/9) {
  .fullscreen-bg__video {
    height: 300%;
    top: -100%;
  }
}
@media (max-aspect-ratio: 16/9) {
  .fullscreen-bg__video {
    width: 300%;
    left: -100%;
  }
}
header .container {
  padding-top: 50px;
  padding-bottom: 50px;
}
header .intro-text .name {
